.node-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(
    135deg,
    #a855f7,
    #3b82f6
  ); /* Gradient background */
  color: white;
  border-radius: 20px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0.5, 0.5);
  padding: 20px 15px;
  margin: 40px;
  width: 185px;
  height: 160px;
  position: relative; /* Ensure relative positioning for the image */
  text-align: center;
  font-family: "Arial", sans-serif;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.node-container:hover {
  transform: scale(1.05);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.3);
}

.node-image {
  width: 70px;
  height: 70px;
  border-radius: 50%; /* Circle image */
  border: 4px solid white; /* Border around the image */
  position: absolute;
  top: -35px; /* Half the height of the image to center it above the node */
  background-color: #f3f4f6; /* Placeholder color if no image */
  background-size: cover;
  background-position: center;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

.node-name {
  font-size: 19px;
  font-weight: bold;
  margin-top: 30px; /* Adjust to account for the image height */
}

.node-title {
  font-size: 15px;
  font-weight: 400;
}

.node-expand-status {
  font-size: 12px;
  margin-top: 5px;
  opacity: 0.7;
}

